<template>
	<view>
		<view class="container">
			<view class="contentPanel" style="border-radius: 18rpx; margin-top: 20rpx;" v-for="(item,index) in bookSkuInfoList" :key="index">
				<view class="booksList">
					<view class="books-info">
						<view class="img">
							<!-- <image  :src= "adminRootUrl + item.imageFilePath" mode="aspectFit"></image> -->
							<view style="position: relative;overflow: hidden;height: 82% ;width: 100%;">
								<view style="cursor: pointer;" @tap.stop="clickToBooksDetail(item)">
									<image :src="adminRootUrl + item.imageFilePath" mode="aspectFit" style=" height: 210rpx;display: block;width: 95% ;  border:  2rpx solid rgb(211 211 211); border-radius: 10rpx;"></image>
									<view class="line50 center f24">{{item.name}}</view>
								</view>
							</view>
							
							<block v-if="item.isOut === true">
								<view class="btnNoBorrow clear">
									不可借
								</view>
							</block> 
							<block v-if="item.isOut === null || item.isOut === 'null' || item.isOut === false">
								<view class="btnBorrow clear" @click="clickBorrow(item,index)">
									借阅
								</view>
							</block> 
						</view>
						<view class="info">
							<view class="name">{{item.bookName}}</view>
							<view class="series" v-for="(ageTtem,ageindex) in item.ageGroupNameList" :key="ageindex">
								<view class="typename" style="">{{ageTtem}}</view>
							</view>
							<view class="series" v-for="(bookTagItem,bookTagindex) in item.bookTagList" :key="bookTagindex+100">
								<view class="typename" style="">{{bookTagItem}}</view>
							</view>
						</view>
						<view>
							<view style="margin-top: 35rpx;" @click="clickCancelMark(item,index)">
								<image :src= "imageRootUrl + 'image/member/bookMark.png'"   class="size100"></image>
								<view class="line40 f24" style="color: #d81e06;">取消收藏</view>
							</view>
						</view>
					</view>
				</view>
			</view>
			<view class="loading-text" style="text-align: center;" v-if="showLoadMore">{{ loadingText }}</view>
			<view class="h40"></view>
		</view>
	</view>
</template>

<script>
	import common from "@/js/common.js";
	import config from "@/js/config.js"; 
	import util from "@/js/util.js";
	let statusBarHeight = uni.getSystemInfoSync().statusBarHeight; // 状态栏 高度
	let that = null;
	export default {
		data() {
			return {
				url:"http://admin.8630.net/WYB/UploadFile/headerTopbg.png",
				imageRootUrl:config.imageRootUrl,//图片地址
				adminRootUrl:config.adminRootUrl +"/",//后端图片地址
				statusBarHeight,// 状态栏 高度
				head_is_fixed:0,
				tabbarIndex: 0,
				typeList: ['可借', '不可借'], //选项卡
				current: 0, //选项卡
				items: ['可借', '不可借'], //选项卡
				styleType: 'button', //选项卡  text  button
				activeColor: '#53D776', //选项卡
				list:[
					{id:1,url:'/static/image/common/bookb1.jpg',name:'密码花园',type:'yes',typeList:[
						{code:'',name:'0-2岁'},{code:'',name:'3-6岁'},{code:'',name:'11-14岁'},{code:'',name:'洞洞翻翻纸板书'},
						{code:'',name:'精装'},{code:'',name:'获奖'},{code:'',name:'国家大师名作套系'},
					]},
					{id:2,url:'/static/image/common/bookb2.jpg',name:'安徒生童话全集',type:'no',typeList:[
						{code:'',name:'0-2岁'},{code:'',name:'3-6岁'},{code:'',name:'11-14岁'},{code:'',name:'绘本认知'},{code:'',name:'洞洞翻翻纸板书'},
						{code:'',name:'精装'},{code:'',name:'获奖'},{code:'',name:'国家大师名作套系'},
					]},
					{id:3,url:'/static/image/common/bookb3.jpg',name:'窗边的小豆豆',type:'yes',typeList:[
						{code:'',name:'0-2岁'},{code:'',name:'绘本认知'},{code:'',name:'洞洞翻翻纸板书'},
						{code:'',name:'精装'},{code:'',name:'国家大师名作套系'},
					]},
					{id:4,url:'/static/image/common/bookb4.jpg',name:'夏洛的网',type:'no',typeList:[
						{code:'',name:'0-2岁'},{code:'',name:'3-6岁'},{code:'',name:'11-14岁'},{code:'',name:'绘本认知'},{code:'',name:'洞洞翻翻纸板书'},
						{code:'',name:'精装'},{code:'',name:'获奖'},{code:'',name:'国家大师名作套系'},
					]},
					{id:5,url:'/static/image/common/bookb5.jpg',name:'柳林风声',type:'yes',typeList:[
						{code:'',name:'0-2岁'},{code:'',name:'3-6岁'},{code:'',name:'11-14岁'},{code:'',name:'绘本认知'},{code:'',name:'洞洞翻翻纸板书'},
						{code:'',name:'精装'},{code:'',name:'获奖'},{code:'',name:'国家大师名作套系'},
					]},
					{id:6,url:'/static/image/common/bookb6.jpg',name:'格林童话',type:'no',typeList:[
						{code:'',name:'0-2岁'},{code:'',name:'3-6岁'},{code:'',name:'11-14岁'},{code:'',name:'绘本认知'},{code:'',name:'洞洞翻翻纸板书'},
						{code:'',name:'精装'},{code:'',name:'获奖'},{code:'',name:'国家大师名作套系'},
					]},
				],
				bookSkuInfoList:[],//收藏列表
				loadingText: "正在加载...",
				showLoadMore: false,
				pageIndex: 0, //页数
				pageSize: 10, //条数
				pageCount: 0, //最大页数
			};
		},
		components: {
			
		},
		onReady() {
			
		},
		onShow() {
			
		},
		onPageScroll(e) {
		    that.head_is_fixed = e.scrollTop > 0 ? 1 : 0
		},
		onPullDownRefresh() { //刷新   //下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
			uni.stopPullDownRefresh();
		},
		onReachBottom() {  //上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
			if ((that.pageIndex + 1) == that.pageCount) {
				that.loadingText = "没有更多数据了";
			} else if ((that.pageIndex + 1) < that.pageCount) {
				that.pageIndex = that.pageIndex + 1;
				this.showLoadMore = true;
				that.loadingText = "加载更多";
				that.bindData();
			}
		},
		onLoad() {
			that = this;
			that.bookSkuInfoList=[]
			that.bindData(); //
		},
		methods: {
			bindData(){
				var data = {};
				data.collectMemberId = 7
				data.orderBy = "create_time"
				data.pageIndex = that.pageIndex 
				data.pageSize = that.pageSize 
				common.call("GetViewBookSkuInfoList", data, function(result) {
					for (let i = 0; i < result.data.dataList.length; i++) {
						var strAge = result.data.dataList[i].ageGroupNames
						
						var strBookTag = result.data.dataList[i].bookTagNames
						if(strAge!="" && strAge!=null && strAge!="null"){
							var arrAge = strAge.split(",");
							console.log(arrAge)
							result.data.dataList[i].ageGroupNameList = arrAge
						}else{
							result.data.dataList[i].ageGroupNameList = []
						}
						if(strBookTag!="" && strBookTag!=null && strBookTag!="null"){
							var arrbookTag = strBookTag.split(",");
							result.data.dataList[i].bookTagList = arrbookTag
						}else{
							result.data.dataList[i].bookTagList = []
						}
					}
					that.bookSkuInfoList.push(...result.data.dataList);
					that.pageCount = result.data.pageCount;
					that.pageIndex = result.data.pageIndex;
					that.pageSize = result.data.pageSize;
					if(that.bookSkuInfoList.length== 0 || that.bookSkuInfoList.length<=10){
						that.showLoadMore =true
						that.loadingText="没有更多数据了" 
					}
					console.log(that.bookSkuInfoList)
				});
			},
			onClickItem(e) { //Tab选项卡切换
				if (this.current !== e.currentIndex) {
					this.current = e.currentIndex
				}
				that.bindData();
			},
			showType(tbIndex) { //状态导航栏切换
				this.tabbarIndex = tbIndex;
				that.bindData();
			},
			clickToBack(){
				uni.navigateBack();
			},
			clickBorrow(item,index){ //借阅
				var data = {};
				data.memberId = 7
				data.companyId = 35
				data.bookSkuId =that.id 
				common.call("AddOnLineBorrowBookOrder", data, function(result) {
					console.log(result)
				}, function(e) {
					console.log(e)
					if (e.success == false) {
						uni.showModal({
							title: "提示",
							content: e.errorMessage,
							showCancel: false,
							confirmText: "确定",
						});
					}
				})
			},
			clickCancelMark(item,index){//取消收藏
				var data = {};
				data.tableName = "member_bookmark";
				data.condition = "member_id = " + 7 +" and book_sku_id=" + item.id
				//[{"key":"data","value":"{tableName:'member_bookmark',condition:'member_id=3 and book_sku_id=35'}","description":"","type":"text","enabled":true}]
				common.call("DeleteMemberBookmark", data, function(result) {
					uni.showModal({
						title: '提示',
						content: "取消成功！",
						showCancel: false,//没有取消按钮的弹框
						success: function (res) {
							if (res.confirm) {
								that.bookSkuInfoList.splice(index, 1);
							} 
					}});
				})
			},
		},
	};
</script>

<style lang="scss">
	@import url("/css/common.css");
	page {
		background: #5EAC59;
	}
	.container{
		
	}
	.tab{
		position: fixed;
		background-color: #fff;
		width: 90%;
		z-index: 99;
	}
	.booksList {
		width: 100%;
		display: block;
		position: relative;
		.books-info {
			display: flex;
			width: calc(100%);
			padding: 20rpx 10rpx 10rpx 10rpx;
			flex-wrap: nowrap;
			border-bottom: 1px solid #dedede;
	
			&:last-child {
				border-bottom: none;
			}
	
			.img {
				width: calc(30vw - 70rpx);
				height: calc(38vw);
				display: inline-block;
				overflow: hidden;
				flex-shrink: 0;
				margin-right: 20upx;
				.btnBorrow {
					width: 130rpx;
					background-color:#53D776;
					height: 50rpx;
					line-height: 50rpx;
					font-size: 26rpx;
					text-align: center;
					color: white;
					border-radius: 10rpx;
					margin: 0px auto;
				}
				.btnNoBorrow{
					width: 130rpx;
					background-color:#FE613D;
					height: 50rpx;
					line-height: 50rpx;
					font-size: 26rpx;
					text-align: center;
					color: white;
					border-radius: 10rpx;
					margin: 0px auto;
				}
				image {
					// width: calc(30vw - 70rpx);
					// height: 210rpx;
					// border-radius: 20rpx;
					// height: 210rpx;
					// display: block;
					// width: 95%;  
					// // border:2rpx solid rgb(211 211 211);
					// border-radius: 20rpx;
				}
			}
	
			.info {
				width: 100%;
				// height: calc(27vw);
				overflow: hidden;
				flex-wrap: wrap;
				align-content: space-between;
				position: relative;
	
				.name {
					width: 100%;
					font-size: 30rpx;
					display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 2;
					overflow: hidden;
					font-weight: bold;
					line-height: 30rpx;
				}
	
				.series {
					.series1 {
						color: rgb(153, 153, 153);
						font-weight: 500;
						font-size: 25rpx;
					}
				}
				.typename{
					background: #F7F8FA; line-height: 30rpx; border-radius: 10rpx; text-align: center;color: #66667B; float: left;
					 padding: 10rpx 15rpx 10rpx 15rpx ; margin-left: 10rpx; margin-top: 10rpx;
					 font-size: 24rpx;
				}
				.borrowTime {
					.borrowTime1 {
						color: rgb(153, 153, 153);
						font-weight: 500;
						font-size: 25rpx;
					}
				}
			}
	
			.countDownPanel {
				width: 130rpx;
				margin-top: 80rpx;
	
				.countDownimg {
					float: left;
	
					image {
						width: 40rpx;
						height: 40rpx;
						margin-top: 5rpx;
					}
				}
	
				.countDownTime {
					float: left;
					color: rgb(153, 153, 153);
				}
			}
			
			.btnCancelNotice {
				width: 130rpx;
				background-color: rgb(254, 97, 61);
				height: 50rpx;
				line-height: 50rpx;
				font-size: 26rpx;
				text-align: center;
				color: white;
				border-radius: 10rpx;
			}
		}
	
	}
	
	.topTabBar {
		border-radius: 10rpx;
		width: 90%;
		position: fixed;
		top: 46px;
		z-index: 10;
		background-color: white;
		height: 80rpx;
		display: flex;
		justify-content: space-around;
		// padding: 20rpx;
	
		.grid {
			width: 20%;
			height: 80upx;
			display: flex;
			justify-content: center;
			align-items: center;
			color: #444;
			font-size: 28upx;
	
			.text {
				height: 76upx;
				display: flex;
				align-items: center;
	
				&.on {
					color: #53D776;
					border-bottom: solid 4upx #53D776;
				}
			}
	
		}
	}
	
	
</style>